<template>
  <div class="introduce-wrapper" ref="introduce">
    <Title :titleImg="img" englistTitle="Product introduction" />
    <div class="model-list" v-for="item in modelList" :key="item.index">
      <Model 
      :mainTitle="item.mainTitle" 
      :explain="item.explain"
      :num="item.index"
      :img="item.img"
      :hover="item.hover"
      :circleColor="item.circleColor"
      :isMobile="isMobile"
      :mobileBg="item.hover"
      :mobileCircle="item.circleColor"
      />
    </div>
    <Footer :isFixed="isFixed"></Footer>  
  </div>
</template>

<script>
import Footer from '@/components/footer/footer.vue'
import Title from '@/components/title/title.vue'
import Model from '@/components/model/model.vue'
export default {
  components:{
    Title,Model,Footer
  },
  data(){
    return{
      isFixed:false,
      isMobile:false,
      img:require('../assets/images/introduce-title.png'),
      modelList:[{
        mainTitle:'3D',
        explain:'3d360 3D3d',
        index:"01",
        img:'',
        hover:'#09D293',
        circleColor:'#1A09D5'
      },{
        mainTitle:'AR',
        explain:'ar',
        index:"02",
        img:'',
        hover:'#0DC7D8',
        circleColor:'#2AFFEA'
      },{
        mainTitle:'AR',
        explain:'ar',
        index:"03",
        img:'',
        hover:'#1866DB',
        circleColor:'#43FFC4'
      }]
    }
  },
  mounted() {
    const h = document.documentElement.clientHeight || document.body.clientHeight;
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    let height = 0;
    
    if(w <= 768){
      height= this.$refs.introduce.offsetHeight + 200;
    }else{
      height= this.$refs.introduce.offsetHeight;
    }

    if(height > h){
      this.isFixed = false
    }else{
      this.isFixed = true
    }

    this.isMobile = w <= 768
  }
}
</script>

<style lang="scss" scoped>
@media (max-width: 768px){
  .introduce-wrapper{
    .model-list{
      margin-bottom:37px;
    }
  }
}
@media (min-width: 768px){
  .introduce-wrapper{
    .model-list{
      width: 1411px;
      margin:42px auto 0 auto;
    }
  }
}
